.header-navigation {
  display: grid;
  grid-template-columns: 1fr;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 24px;
  font-weight: 700;
  display: none;
}
.header-navigation__wrapper {
  position: relative;
}
.header-navigation__button {
  position: absolute;
  top: -55px;
  right: 20px;
  z-index: 2;
  width: 30px;
  height: 30px;
  cursor: pointer;
  border: none;
  background-color: #ffffff;
}
.header-navigation__wrapper--closed .header-navigation__button::before {
  content: "";
  position: absolute;
  top: 6px;
  right: 5px;
  width: 20px;
  height: 2px;
  background-color: #231f20;
  box-shadow: 0 8px 0 0 #231f20, 0 16px 0 0 #231f20;
}
.header-navigation__wrapper--closed .header-navigation__button:active::before {
  background-color: rgba(35, 31, 32, 0.3);
  box-shadow: 0 8px 0 0 rgba(35, 31, 32, 0.3), 0 16px 0 0 rgba(35, 31, 32, 0.3);
}
.header-navigation__wrapper--closed ~ .header-navigation__link {
  display: none;
}
.header-navigation__wrapper--open .header-navigation__button::before,
.header-navigation__wrapper--open .header-navigation__button::after {
  content: "";
  position: absolute;
  top: 14px;
  left: 2px;
  width: 27px;
  height: 2px;
  background-color: #000000;
}
.header-navigation__wrapper--open .header-navigation__button::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.header-navigation__wrapper--open .header-navigation__button::after {
  transform: rotate(-45deg);
}
.header-navigation__wrapper--open .header-navigation__button:active::before,
.header-navigation__wrapper--open .header-navigation__button:active::after {
  background-color: rgba(0, 0, 0, 0.3);
}
