.modal {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.9);
}
.modal__close {
  display: none;
}
.modal__window {
  position: relative;
  margin: auto;
  padding: 28px 30px 46px;
  background-color: #ffffff;
  box-shadow: 0 5px 10px 0 rgba(0, 1, 1, 0.25);
}
.modal__add-basket {
  width: 320px;
  box-sizing: border-box;
}
.modal__title {
  margin: 26px 0 14px;
  padding: 0 52px 0 53px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 25px;
  line-height: 30px;
  font-weight: 700;
  color: #000000;
  text-align: center;
  text-transform: uppercase;
}
.modal_text {
  margin: 0;
  padding: 0 60px 22px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 30px;
  font-weight: 400;
  color: #444444;
  text-align: center;
}
.modal__size {
  display: flex;
  justify-content: space-between;
  text-align: center;
}
.modal__size-button {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 16px;
  font-weight: 700;
  border: 2px solid rgba(68, 68, 68, 0.1);
  padding: 16px 34px;
  text-decoration: none;
  color: #444444;
  text-transform: uppercase;
}
.modal__size-button:hover {
  border: 2px solid #63D1BB;
}
.modal__size-button:active {
  border: 2px solid #63D1BB;
}
.modal__button {
  display: block;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 16px;
  font-weight: 700;
  background-color: #63d1bb;
  padding: 18px 68px;
  text-decoration: none;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
  margin-top: 31px;
}
.modal__button:hover {
  background-color: #444444;
  color: #ffffff;
}
.modal__button:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .modal__window {
    box-shadow: 0 5px 10px 0 rgba(0, 1, 1, 0.25);
  }
  .modal__add-basket {
    width: 566px;
  }
  .modal__title {
    font-size: 30px;
    line-height: 57px;
    margin: 10px 0;
    margin-bottom: 12px;
  }
  .modal_text {
    font-size: 17px;
    padding-bottom: 19px;
  }
  .modal__size {
    padding: 0 78px;
  }
  .modal__size-button {
    padding: 16px 49px;
  }
  .modal__size-button-m {
    padding: 16px 46px;
  }
  .modal__button {
    margin: 31px 78px 0;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
body {
  font-family: "Open Sans", "Arial", sans-serif;
  min-width: 320px;
  margin: 0;
}
